@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
}

html {
  color: #3d4e61;
  margin: 0;
  font-family: Avenir, Arial, Helvetica, sans-serif;
  font-weight: 300;
}

.main {
  flex-direction: column;
  margin: 0;
  display: flex;
}

.main__search-bar {
  margin-top: 20px;
  margin-inline: 8px;
}

.search-bar {
  border: 1px solid var(--sb-border-color);
  border-radius: 4px;
  align-items: center;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar:hover {
  box-shadow: 0 3px 6px var(--sb-border-color);
}

.search-bar:not(:first-of-type) {
  margin-top: 20px;
}

.search-bar:has(.search-bar__input:focus) {
  background: var(--sb-focus-background);
  box-shadow: 0 3px 6px var(--sb-border-color);
  border-radius: 3px;
}

.search-bar__icon {
  height: var(--sb-icon-size);
  width: var(--sb-icon-size);
  margin: var(--sb-icon-margin);
  background-image: url("Search.ccd3c07b.svg");
  background-position: center;
  background-size: cover;
}

.search-bar__input {
  font-size: var(--sb-input-font-size);
  line-height: calc(var(--sb-height)  - 1px);
  color: inherit;
  background: inherit;
  border: none;
  flex-grow: 1;
  padding: 1px 0 0;
  font-family: inherit;
}

.search-bar__input::placeholder {
  color: inherit;
}

.search-bar__input:focus {
  background: var(--sb-focus-background);
  outline: none;
  font-weight: 900;
}

.search-bar {
  --sb-focus-background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  --sb-border-color: #e1e7ed;
}

.search-bar--big {
  --sb-height: 68px;
  --sb-icon-size: 19px;
  --sb-icon-margin: 24px 18px 25px 25px;
  --sb-input-font-size: 16px;
}

.search-bar--small {
  --sb-height: 40px;
  --sb-icon-size: 11px;
  --sb-icon-margin: 14px 10px 15px 12px;
  --sb-input-font-size: 14px;
}
